import _ from 'lodash'
import api from 'api'
import { Account, common, config } from 'librarys/lightcone_v3';
import utils from 'utils'
const BigNumber = require('bignumber.js');
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'

const initState = {
  list: [],
  page: 1,
  pageSize: 10
}

function formatBalances (balances) {
  let tokens = config.getTokens()
  return balances.map(balance => {
    let { tokenId, totalAmount, frozenAmount } = balance
    let token = _.find(tokens, { id: tokenId })
    if (!token) return

    balance.token = token
    let availAmount = BigNumber(totalAmount) - BigNumber(frozenAmount)
    // 不能用 fromWEI, 因为 fromWEI用了 toFixed,会导致四舍五入。全部划转会超。
    // 优化，这里应该是原始数据，只是界面上四舍五入。否则全部划转会少一部分。
    balance.availAmount = BigNumber(availAmount).div("1e" + token.digits).decimalPlaces(token.precision).toNumber()
    balance.totalAmount = BigNumber(totalAmount).div("1e" + token.digits).decimalPlaces(token.precision).toNumber()
    balance.frozenAmount = BigNumber(frozenAmount).div("1e" + token.digits).decimalPlaces(token.precision).toNumber()
    return balance
  }).filter(balance => !!balance)
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchBalances (payload, { dexAccount }) {
      let { accountId, apiKey } = dexAccount
      if (!accountId || !apiKey) return

      let { skip, limit } = utils.formatPageParams(dexAccount)
      let balances = await api.getBalances(accountId, apiKey, skip, limit)
      if (!balances) return

      balances = formatBalances(balances)
      this.update({ list: balances })
    },
    // 账户发生变动时。登录，重置密码，需要重新订阅账户。
    startFetchBalance (payload, { dexAccount }) {
      let { accountId, state } = dexAccount
      if (!accountId || !ENUM_ACCOUNT_STATUS.LOGGED_IN.equals(state)) return
      console.info(`======开始获取 账户 信息======`)

      dispatch.balance.fetchBalances()
      dispatch.socket.subscribe([
        // `candlestick&${market}&1Hour`,
        `account`,
      ])
    },
    async onSocketReady (payload, { markets, depth }) {
      dispatch.balance.startFetchBalance()
    },
    async incrementalUpdate (payload, { balance }) {
      let balances = balance.list

      let [newBalance] = formatBalances([payload])
      let list = _.unionBy([newBalance], balances, b => b.tokenId)
      this.update({ list })
    },
  })
}