import { ethereum, config } from 'librarys/lightcone_v3';

import {
  Box,
  Flex,
  Text
} from 'rebass';
import funcfy from './funcfyDialog';
import { ENUM_ACCOUNT_STATUS } from 'utils/enum';
import utils from 'utils';
import { FormErrorInfo } from 'widgets/Common';
import { UpsertPasswordModal } from '.';
import api from 'api';

const { TextArea } = Input;

const errorInfos = [
  '大写字母',
  '小写字母',
  '数字',
  '至少8个字符'
];

class Widget extends Component {
  constructor(props) {
    super(props);

    this.state = {
      visible: false
    };
  }

  componentDidMount() {
    this.setState({ visible: true });
    this.checkInput();
  }

  async checkInput() {
    // 必须稍微等下，因为被接管后的表单虽然可以继续使用onChange事件，但是onChange触发时，表单的校验结果可能还没做完。
    // await utils.waitFor(50)

    // // 修改表单校验的规则：如果没有输入任何东西，默认全部错误。而表单校验本身的规则是，不输入，则无错误。
    // let password = this.props.form.getFieldProps('password').value
    // if (!password) {
    //   this.setState({errors: errorInfos})
    //   return 
    // }

    // let errors = this.props.form.getFieldsError()
    // errors = _.chain(errors)
    //   .values()
    //   .flatten()
    //   .filter(error => error)
    //   .value()
    // this.setState({errors})
  }

  async submit(e) {
    //  let { dexAccount } = this.props;
    this.props.clearAccountInfo();

    e.preventDefault();

    let validateFields = utils.promisefy(this.props.form.validateFields);
    let values;
    try {
      values = await validateFields();
    } catch (err) {
      return;
    }
    let { jsonApi } = values;

    jsonApi = JSON.parse(jsonApi);

    let passwordHash = jsonApi.passwordHash;
    let accountId = jsonApi.accountId;

    let keyPair = {
      publicKeyX: jsonApi.publicKeyX,
      publicKeyY: jsonApi.publicKeyY,
      privateKey: jsonApi.privateKey
    };
    let apiKey = jsonApi.apiKey;

    let address = jsonApi.accountAddress;

    window.wallet = ethereum.account.fromMetaMask(window.web3, address, address);
    await this.props.updateAccount({
      address,
      accountId,
      passwordHash,
      apiKey,
      keyPair,
      rememberMeStartTime: moment().format('YYYY-MM-DD HH:mm:ss'),
      rememberMeMinutes: 24 * 60 * 360
    });


    this.props.updateAccount({ state: ENUM_ACCOUNT_STATUS.LOGGED_IN.getValue() });
    this.props.startFetchBalance();
    this.props.fetchOrders();

    notification['success']({
      duration: 1,
      message: '登录成功'
    });
    this.props.startSocket()
    this.loginSunccess();

    // setTimeout(function() {
    //
    //   window.location.reload(false);
    //
    // }, 5000);


  }

  async loginSunccess() {
    this.setState({ visible: false });
    this.props.onSucess();
    this.props.onDestory();
  }

  render() {
    let { state } = this.props.dexAccount;
    let modalTitle = '导入Api';

    const { getFieldDecorator, getFieldsValue } = this.props.form;
    let { jsonApi } = getFieldsValue();

    const formItemLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 20 }
    };
    const noLabelLayout = {
      wrapperCol: { span: 19, offset: 5 }
    };
    return (
      <Modal
        // bodyStyle={{backgroundColor: '#363636', color: '#d0d0d0'}}
        title={modalTitle}
        visible={this.state.visible}
        maskClosable={true}
        onOk={() => {

        }}
        onCancel={() => {
          this.setState({ visible: false });
          this.props.onFail();
          this.props.onDestory();
        }}
        footer={null}
        width={660}
        className="password-modal"
      >
        <Box pt={60} pb={68} justifyContent="center">
          <Box width={480} mx="auto">
            <Form className="upsert-password-form" onSubmit={(e) => {
              utils.withLoading(() => this.submit(e));
            }}>
              <Box>
                <Form.Item {...noLabelLayout}>
                  <Flex mt={10} justifyContent="space-between">
                    {getFieldDecorator('jsonApi', {
                      initialValue: false
                    })(
                      <TextArea rows={10} onChange={() => this.checkInput()}/>
                    )}
                  </Flex>
                  {jsonApi && (
                    <Text lineHeight={'30px'} fontSize={14} color="#969696" ml={'6px'}>24 小时内交易无需输入密码</Text>)}
                </Form.Item>
              </Box>
              <Box mt={15}>
                <Form.Item {...noLabelLayout}>
                  <Text color="#F0505A" lineHeight="14px">内容为JSON格式,刷新后需重新填入</Text>
                </Form.Item>
              </Box>


              <Box mt={26}>
                <Form.Item {...noLabelLayout}>
                  <Button size="large" style={{ width: '100%' }} type="primary" htmlType="submit">确定</Button>
                </Form.Item>
              </Box>
            </Form>
          </Box>
        </Box>
      </Modal>
    );
  }
}

const WrappedForm = Form.create({ name: 'upsert_password_form' })(Widget);

const mapStateToProps = ({ metaMask, dexAccount }) => {
  return {
    metaMask,
    dexAccount
  };
};
const mapDispatchToProps = ({ dexAccount, balance, order,socket }) => {
  return {
    updateAccount: dexAccount.updateAccount,
    startFetchBalance: balance.startFetchBalance,
    fetchOrders: order.fetchOrders,
    clearAccountInfo: dexAccount.clearAccountInfo,
    startSocket: socket.start

  };
};
let connectedWidget = connect(mapStateToProps, mapDispatchToProps, null, { forwardRef: true })(WrappedForm);

export default function promisefy(props) {
  return new Promise((resolve, reject) => {
    funcfy(connectedWidget, {
      ...props,
      onSucess: resolve,
      onFail: reject
    });
  });
} 