import _ from 'lodash'
import { Account, config, exchange } from 'librarys/lightcone_v3';
import utils, { notification } from 'utils'
import api from 'api'
const BigNumber = require('bignumber.js');

const initState = {
  buyPrice: null,
  buyAmount: null,
  buyTotal: null,

  sellPrice: null,
  sellAmount: null,
  sellTotal: null,

  // 市价买入的”交易额“
  marketBuyTotal: null,
  // 市价卖出的”数量“
  marketSellAmount: null,

  channelName: null,
}

async function placeAnOrder (isBuy, tokenS, tokenB, amountS, amountB, dexAccount, channelName) {
  
   let owner = window.wallet.account
  let { accountId, passwordHash, apiKey,keyPair } = dexAccount


  console.log("leven55",dexAccount,passwordHash)

  let exchangeAccount = new Account(window.wallet);
  const keyPair1 = exchangeAccount.generateKeyPair(passwordHash);
 
  let publicKeyX,publicKeyY,tradingPrivKey
  if(  passwordHash==12345  ){
    
    publicKeyX=keyPair.publicKeyX
    publicKeyY=keyPair.publicKeyY
    tradingPrivKey=keyPair.privateKey
    console.log("afasdf")
  }else{
    publicKeyX=keyPair1.publicKeyX
    publicKeyY=keyPair1.publicKeyY
    tradingPrivKey=keyPair1.privateKey
  }
  
  const tokenSConfig = config.getTokenBySymbol(tokenS)
  const orderId = await api.getOrderId(accountId, tokenSConfig.id, apiKey);
  
  // Timestamp in second
  const validSince = new Date().getTime() / 1000 - 3600;
  const validUntil = new Date().getTime() / 1000 + 3600 * 24 * 30;

  console.info(`下单:`, {amountB, amountS})

  const signedEtheruemTx = exchange.submitOrder(window.wallet, {
    owner,
    accountId,
    keyPair: {
      publicKeyX,
      publicKeyY,
      secretKey: tradingPrivKey,
    },
    tokenS,
    tokenB,
    amountS,
    amountB,
    orderId,
    validSince: Math.floor(validSince),
    validUntil: Math.floor(validUntil),
    buy: isBuy
  })

  if (channelName) {
    signedEtheruemTx.channelId = `wedex::subchannel::${channelName}`
  }

  console.log('Order signed tx: ', signedEtheruemTx);
  const response = await api.submitOrderToLightcone(signedEtheruemTx, apiKey);
  console.log('Response of submitOrder: ', response);

  if (!response) return
  notification['success']({
    duration: 5,
    message: '下单成功'
  });
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    // channelName 不能清除
    initialize: (state, payload) => ({ ...initState, channelName: state.channelName }),
  },
  effects: dispatch => ({
    async checkChannel (payload, { router }) {
      const query = utils.getUrlParams(router.location.search)
      const { channel } = query
      if (channel) {
        this.update({ channelName: channel })
      }
    },
    async placeBuyOrder (payload, { markets, dexAccount, placeOrder }) {
      let { baseToken: tokenB, quoteToken: tokenS } = markets.currentMarket
      let { buyAmount, buyPrice, channelName } = placeOrder
      if (!buyAmount || !buyPrice) return

      buyAmount = new BigNumber(buyAmount);
      let amountS = buyAmount.times(buyPrice).toFixed();
      let amountB = buyAmount.toFixed();

      let result = utils.withLoading(() => placeAnOrder(true, tokenS, tokenB, amountS, amountB, dexAccount, channelName))
      return result
    },
    async placeSellOrder (payload, { markets, dexAccount, placeOrder }) {
      let { baseToken: tokenS, quoteToken: tokenB } = markets.currentMarket
      let { sellAmount, sellPrice, channelName } = placeOrder
      if (!sellAmount || !sellPrice) return

      sellAmount = new BigNumber(sellAmount);
      let amountB = sellAmount.times(sellPrice).toFixed();
      let amountS = sellAmount.toFixed();

      let result = utils.withLoading(() => placeAnOrder(false, tokenS, tokenB, amountS, amountB, dexAccount, channelName))
      return result
    },
  })
}